.is-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

body {
  background-color: #f0f8ff;
}

img {
  border-bottom: 1px solid #afafaf;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 30px;
  margin-bottom: 20px;
  display: flex;
}

.search-form {
  text-align: center;
  margin: 20px auto;
}

.search-form input[type="text"] {
  width: 200px;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 10px;
  box-shadow: 4px 4px 8px rgba(0, 0, 0, .4);
}

.search-form .search-btn {
  color: #fff;
  cursor: pointer;
  background-image: linear-gradient(45deg, rgba(252, 128, 128, .706), #8181fc);
  border: none;
  border-radius: 5px;
  margin-left: 10px;
  padding: 10px 20px;
  transition: background-color .2s;
  box-shadow: 4px 4px 8px rgba(0, 0, 0, .4);
}

.search-btn:active {
  background-color: rgba(0, 0, 0, .8);
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-image: linear-gradient(45deg, rgba(252, 128, 128, .706), #8181fc);
  border: none;
  border-radius: 5px;
  margin-left: auto;
  margin-right: auto;
  padding: 10px 20px;
  transition: background-color .2s;
  display: block;
  box-shadow: 4px 4px 8px rgba(0, 0, 0, .4);
}

.load-more:active {
  background-color: rgba(0, 0, 0, .8);
}

.photo-card {
  color: #000;
  background-image: linear-gradient(45deg, rgba(253, 209, 209, .706), #c4c4ff);
  border: 1px solid #afafaf;
  border-radius: 5%;
  text-decoration: none;
  list-style: none;
  overflow: hidden;
  box-shadow: 4px 4px 8px rgba(0, 0, 0, .4);
}

.info {
  text-align: center;
  font-family: Oswald, sans-serif;
  font-size: 14px;
}

